@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;800;900&display=swap";
*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

.navbar {
  padding: 0;
}

body {
  color: #343a40;
  background-image: linear-gradient(135deg, #9b6464 0%, #3b52a3 100%);
  padding-top: 70px;
  font-family: Inter, sans-serif;
  line-height: 1;
}

h3 {
  letter-spacing: -.5px;
  margin: 0;
  font-size: 24px;
}

header {
  color: #fff;
