section {
  background-color: #fff;
  border: 1px solid #9b6464;
  border-radius: 5px;
  margin: 2rem;
  padding: 2rem;
  position: relative;
  box-shadow: 0 13px 4px #0000001a;
}

a.nav-link, .copyright-text {
  text-transform: uppercase;
  color: #6065ac;
  opacity: 1;
  margin: 0;
  font-weight: 900;
  transition: opacity .3s ease-in-out;
}

a.nav-link:hover {
  color: #fff;
  opacity: .9;
  background-image: linear-gradient(135deg, #9b6464 0%, #3b52a3 100%);
  border-radius: 5%;
}

form input {
  margin-bottom: 5px;
}

form label {
  padding: 5px;
}

form button {
  margin-top: 5px;
}

.just-validate-error-label {
  padding: 5px;
}

section p {
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.6;
}

.card-text {
  font-size: .9rem;
}

.btn-primary {
  background-color: #4a69bd;
}

.card {
  width: 96%;
  height: 420px;
  margin: 5px auto;
}

.left {
  font-size: 30px;
  position: absolute;
  top: 50%;
  right: 95.5%;
}

.right {
